.main {
    width: 100%;
    height: 100vh;
}


.main__wrapper {
    padding: var(--vertical-padding) 10%;
}

.wrapper__counter {
    margin-top: 5%;
}

.content {
    display: flex;
    width: 100%;
    margin-top: var(--vertical-margin);
}

.left__content {
    display: flex;
    flex-direction: column;
    gap: 35px;
}

.content__title {
    font-size: 56px;
    font-weight: 500;
}

.content__text {
    display: flex;
    flex-direction: column;
    gap: 35px;
    font-size: 36px;
    font-weight: 400;
}

.left {
    display: flex;
    flex-direction: column;
    width: 50%;
    margin-top: 50px;
    gap: 20px;
}

.right {
    width: 50%;
}

.header {
    width: 100%;
    display: flex;
    justify-content: space-between;
    height: var(--header-height);
}

.header__counter {
    text-align: center;
}

.header__title {
    font-size: 64px;
}
.img {
    height: 100%;
    display: flex;
    justify-content: center;
}

.img__wrapper {
    display: flex;
    align-items: center;
    height: 100%;
    max-height: 800px;
    position: relative;
    justify-content: center;
    width: fit-content;
}

.img img {
    height: 100%;
}

.gradient {
    position: absolute;
    z-index: -1;
}

.star {
    display: flex;
    position: absolute;
    height: 15%;
}

.downloadButtons {
    margin-top: 5%;
    display: flex;
    gap: 20px;
}

#star__top {
    top: -5%;
    left: 15%;
}

#star__center {
    height: 10%;
    bottom: 20%;
    right: 10%;
}

#star__bottom {
    height: 7%;
    bottom: 40%;
    right: 5%;
}

@media (max-width: 770px) {
    .img__wrapper {
        max-height: 500px;
    }

    .content__title {
        font-size: 34px;
    }

    .content__text {
        font-size: 22px;
    }
    .left {
        gap: 30px;
    }

    .header__title {
        font-size: 42px;
    }

}
@media (min-width: 1000px) {

    .content {
        margin: 0;
    }

    .img__wrapper {
        max-height: 600px;
    }

    .content__title {
        font-size: 46px;
    }

    .content__text {
        font-size: 30px;
    }

}

@media (max-width: 1440px) {
    #text__1 {
        display: none;
    }
}

@media (min-width: 770px) and (max-width: 1000px) {
    .img__wrapper {
        max-height: 600px;
    }

    .content__title {
        font-size: 36px;
    }

    .content__text {
        font-size: 26px;
    }

    .counter {
        max-width: 200px;
    }
}

@media (max-width: 600px) {

    .ticker-view::before {
        padding: 0;
    }
    .ticker-view > .ticker-column-container {
        font-size: 30px;
    }

    .wrapper__counter {
        margin: 0;
    }

    .downloadButtons {
        margin: 0;
    }

    .button {
        min-width: 30%;
    }

    .content__title {
        font-size: 30px;
    }

    .content {
        flex-direction: column;
        margin: 0;
    }

    .left, .right {
        width: 100%;
        margin: 0;
    }

    .left__content {
        gap: 15px;
    }

    .img {
        padding-top: 15%;
        height: 37vh;
    }

    .counter {
        height: 50px;
    }

    #text__2 {
        display: none;
    }

    .header {
        height: 50px;
    }

}

@media (min-height: 900px) {
    .img__wrapper {
        max-height: 800px;
    }
}

@media (max-width: 600px) and (max-height: 700px) {
    .content__title {
        font-size: 32px;
    }

    .header {
        height: 10vh;
    }
}




